@use "./../../foundation/variable" as *;

.p-theme {
  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    gap: 32px;
    img {
      width: 420px;
      height: 320px;
      object-fit: cover;
    }
  }
  &__lists {
    font-size: 2rem;
    li {
      position: relative;
      padding-left: 24px;
      margin: 10px;
      &:before {
        font-family: "Font Awesome 5 Free";
        content: "\f00c";
        font-weight: 900;
        left: 0;
        position: absolute;
      }
    }
  }
}
