@use "./../foundation/variable" as *;

.l-footer {
  background-color: $color-black;
  color: $color-base;

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 30px 0;
  }
  &__lists {
    display: flex;
    gap: 10px;
    padding: 0 0 30px;
    a {
      color: $color-base;
    }
  }
}
